import { Directive, Input, OnInit } from '@angular/core';
import { FormControl } from '@angular/forms';
import { FormComponent } from '@sofico-framework/ui-kit/components/form';

@Directive({
  selector: '[sofInput]'
})
export class InputDirective implements OnInit {
  @Input() sofInput;
  @Input() formControl: FormControl;

  @Input() set formControlName(name: string) {
    throw new Error(
      'You should use the [formControl] directive instead of the formControlName directive'
    );
  }

  @Input() errorMap: {
    [key: string]: string;
  };

  constructor(public form: FormComponent) {}

  ngOnInit(): void {
    if (!this.form) {
      throw new Error(
        'The [sofInput] directive should be used inside a sof-form element'
      );
    }
  }
}
